import * as React from "react";
import Button from "../Button";
import HStack from "../HorizontalBox";

import {
    Modal,
    ModalBody,
    ModalFooter,
} from "../Modal";

import { useDisclosure } from "../../../hooks/useDisclosure";

const PASSWORD_TO_FORM_MAP = {
    'test-01': 'https://forms.gle/u1rEn8RoKGuGWfcy8',
};

const RSVPButton = () => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const [value, setValue] = React.useState('');
    const handleChange = (event) => setValue(event.target.value)

    const openRSVP = () => {
        const password = localStorage.getItem('rsvp_password');

        if (!password) {
            onOpen();
        } else {
            const formUrl = PASSWORD_TO_FORM_MAP[password];
            window.open(formUrl, '_newtab');
        }
    };

    const enterPassword = () => {
        const trimmedValue = value.trim();
        const formUrl = PASSWORD_TO_FORM_MAP[trimmedValue];
        if (formUrl) {
            localStorage.setItem('rsvp_password', trimmedValue);
            window.open(formUrl, '_newtab');
            onClose();
        }
    };

    return (
        <>
            <HStack p={8} justifyContent="center" alignItems="center">
                <Button onClick={openRSVP}>RSVP</Button>
            </HStack>

            <Modal isOpen={isOpen} onClose={onClose} title={"RSVP"}>
                <ModalBody>
                    <input
                        value={value}
                        onChange={handleChange}
                        placeholder='Enter guest password'
                    />
                </ModalBody>

                <ModalFooter>
                    <Button variant='ghost' mr={3} onClick={onClose}>
                        Close
                    </Button>

                    <Button variant='ghost' onClick={enterPassword}>
                        Enter
                    </Button>
                </ModalFooter>
            </Modal>
        </>
    );
};

export default RSVPButton;